import React from "react";
import Container from "muicss/lib/react/container";
import Modal from "react-modal";
import LendingCriteria from "../lendingCriteria/LendingCriteria";

class QTSLendingCriteria extends React.Component {
  render() {
    return (
      <div>
        {this.props.loading ? (
          <Modal
            className="loading-modal"
            isOpen={true}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        ) : (
          <div>
            <div>
              <Container></Container>
            </div>
            {this.props.lendingPrograms &&
            this.props.lendingPrograms.length >= 1 ? (
              this.props.lendingPrograms.map((item, index) => (
                <LendingCriteria
                  key={index}
                  index={index}
                  item={item}
                  authentication={this.props.authentication}
                  isQtsPage={this.props.isQtsPage}
                  lists={this.props.lists}
                />
              ))
            ) : (
              <div>
                <div className="grey-circle">
                  <i className="material-icons">warning</i>
                </div>
                <h2 className="no-results">
                  No Lending Criteria for this user
                </h2>
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default QTSLendingCriteria;
