import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as qtsActions from "../../actions/qtsActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import LendingCriteriaPage from "./QTSLendingCriteria";
import getParameterByName from "../common/getParameterByName";
import QTSQuotesContainer from "./QTSQuotesContainer";

class LendingCriteriaContainer extends React.Component {
  componentDidMount() {
    this.props.actions.getParameterListByBank(getParameterByName("bankId"));
    this.props.lcactions.getLendingTypes();
    this.props.lcactions.getLendingCriteriaLists();
    this.props.lcactions.getRecourseTypes();
  }

  render() {
    const {
      lendingTypes,
      maxTermTypes,
      minDscrTypes,
      maxInterestOnlyTypes,
      prePaymentPenaltyTypes,
      maxInitialTermTypes,
      maxPermAfterConstructionTypes,
      maxAmortizationTypes,
      recourseTypes,
      simpleRateSheetTypes,
    } = this.props.lendingCriteria;
    return (
      <QTSQuotesContainer>
        <div>
          <LendingCriteriaPage
            isQtsPage={true}
            lendingPrograms={this.props.qts.lendingList}
            loading={this.props.qts.loadingLC}
            authentication={JSON.parse(localStorage.getItem("data"))}
            lists={{
              maxTermTypes,
              minDscrTypes,
              maxInterestOnlyTypes,
              prePaymentPenaltyTypes,
              maxInitialTermTypes,
              lendingTypes,
              maxPermAfterConstructionTypes,
              maxAmortizationTypes,
              recourseTypes,
              simpleRateSheetTypes,
            }}
          />
        </div>
      </QTSQuotesContainer>
    );
  }
}

function mapStateToProps(state) {
  return {
    lendingCriteria: state.lendingCriteria,
    qts: state.QTS,
    title: state.navTitle,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    lcactions: bindActionCreators(lendingCriteriaActions, dispatch),
    actions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LendingCriteriaContainer);
