import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Link, NavLink } from "react-router-dom";
import * as qtsActions from "../../actions/qtsActions";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";

class QTSQuotes extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpened: false,
      modalOpen: false,
      contact: {},
      bankerName: "",
      bankerId: "",
    };
  }

  render() {
    if (this.props.qts.quotes && !this.props.qts.loadingQuotes) {
      var bankName = (
        <p className="bank-name">{this.props.qts.quotes.bank_name}</p>
      );
    }

    return (
      <Row>
        <Row className="sub-header">
          <Col lg="12" md="12" sm="12">
            <Link
              to={{ pathname: `/qts-banks`, search: window.location.search }}
              className="back-arrow"
            >
              <img src="/images/icons/arrow-left.png" alt="arrow-left" />
            </Link>
            <ul className="qts-nav">
              <li className="qts-nav-list-item list-item2">
                <NavLink
                  to={{
                    pathname: `/qts-quotes`,
                    search: window.location.search,
                  }}
                  className="qts-nav-item"
                  activeClassName="header-active-link"
                >
                  Quotes Received
                </NavLink>
              </li>
              <li className="qts-nav-list-item list-item3">
                <NavLink
                  to={{
                    pathname: `/qts-lending-criteria`,
                    search: window.location.search,
                  }}
                  className="qts-nav-item"
                  activeClassName="header-active-link"
                >
                  Lending Criteria
                </NavLink>
              </li>
            </ul>
          </Col>
        </Row>
        <Row>
          <div className="qts-container">
            {bankName}
            {this.props.children}
          </div>
        </Row>
      </Row>
    );
  }
}

function mapStateToProps(state) {
  return {
    qts: state.QTS,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    qtsActions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QTSQuotes);
